import { Component } from "react"
import Navbar from "react-bulma-components/esm/components/navbar"
import Icon from "react-bulma-components/esm/components/icon"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import faAsterisk from "@fortawesome/fontawesome-free-solid/faAsterisk"
import faArchive from "@fortawesome/fontawesome-free-solid/faArchive"
import faLock from "@fortawesome/fontawesome-free-solid/faLock"
import faCopyright from "@fortawesome/fontawesome-free-solid/faCopyright"
import React from "react"
import { graphql, StaticQuery } from "gatsby"

class MenuNavbar extends Component {
  state = { active: false }

  handleClick = () => {
    const { active } = this.state
    this.setState({ active: !active })
  }

  render() {
    return (
      <StaticQuery
        query={graphql`
          query {
            allNewJson(sort: { fields: published, order: DESC }, limit: 1) {
              edges {
                node {
                  published
                  title
                  slug
                  tags
                  newsletter {
                    welcome
                    intro
                    parts {
                      emoji {
                        label
                        icon
                      }
                      title
                      tags
                      text
                    }
                    outro
                  }
                }
              }
            }
          }
        `}
        render={(data) => {
          return (
            <Navbar active={this.state.active} onClick={this.handleClick}>
              <Navbar.Burger />
              <Navbar.Menu>
                <Navbar.Container align={"left"}>
                  <Navbar.Item href={`/${data.allNewJson.edges[0].node.slug}`}>
                    <Icon style={{ marginRight: "5px" }}>
                      <FontAwesomeIcon align="left" icon={faAsterisk} />
                    </Icon>
                    Aktuell
                  </Navbar.Item>
                  <Navbar.Item href="/archiv">
                    <Icon style={{ marginRight: "5px" }}>
                      <FontAwesomeIcon align="left" icon={faArchive} />
                    </Icon>
                    Archiv
                  </Navbar.Item>
                </Navbar.Container>
                <Navbar.Container align={"right"}>
                  <Navbar.Item href="/datenschutz">
                    <Icon style={{ marginRight: "5px" }}>
                      <FontAwesomeIcon align="left" icon={faLock} />
                    </Icon>
                    Datenschutz
                  </Navbar.Item>
                  <Navbar.Item href="/impressum">
                    <Icon style={{ marginRight: "5px" }}>
                      <FontAwesomeIcon align="left" icon={faCopyright} />
                    </Icon>
                    Impressum
                  </Navbar.Item>
                </Navbar.Container>
              </Navbar.Menu>
            </Navbar>
          )
        }}
      />
    )
  }
}

export default MenuNavbar
