import React from "react"
import { Link, useStaticQuery, graphql } from "gatsby"
import Salamanda from "../../static/supasalamanda.svg"
import Image from "react-bulma-components/esm/components/image"
import Box from "react-bulma-components/esm/components/box"
import Heading from "react-bulma-components/esm/components/heading"
import Tag from "react-bulma-components/esm/components/tag"
import IframeResizer from "iframe-resizer-react"
import ConsentContainer from "./consentContainer"

const NewsletterSubscribe = () => {
  const data = useStaticQuery(graphql`
    query {
      allNewJson(sort: { fields: published, order: DESC }, limit: 1) {
        edges {
          node {
            published
            title
            slug
            tags
            newsletter {
              welcome
              intro
              parts {
                emoji {
                  label
                  icon
                }
                title
                tags
                text
              }
              outro
            }
          }
        }
      }
    }
  `)
  return (
    <Box textAlign={"center"} style={{ boxShadow: "none" }}>
      <Link to="/">
        <Image
          src={Salamanda}
          alt={"Voll kig, Diggi!"}
          title={"Voll kig, Diggi!"}
          className={"logo"}
        />
      </Link>
      <Heading>Ivo's Ecotainment Newsletter</Heading>
      <Heading subtitle size={6} renderAs={"h2"}>
        Einmal im Monat! Trag Dich ein! Aktuell: &nbsp;
        {data.allNewJson.edges.map(({ node }, index) => (
          <Link key={index} to={`/${node.slug}`}>
            {node.title}
          </Link>
        ))}
      </Heading>
      <IframeResizer
        heightCalculationMethod="lowestElement"
        style={{
          width: "100%",
          maxWidth: "600px",
          minHeight: "20px",
          overflow: "hidden",
          height: "181px",
        }}
        src="https://xpw0i.mjt.lu/wgt/xpw0i/zx2/form?c=329f4907"
      />
      <script
        type="text/javascript"
        src="https://app.mailjet.com/pas-nc-embedded-v1.js"
      ></script>
      <div>
        <Tag.Group style={{ display: "inline" }}>
          <Tag>#Umwelt</Tag>
          <Tag>#Wissenschaft</Tag>
          <Tag>#Aktivisten</Tag>
          <Tag>#Birding</Tag>
          <Tag>#Subkultur</Tag>
          <Tag>#Trivia</Tag>
          <Tag>#Lulz</Tag>
        </Tag.Group>
      </div>
    </Box>
  )
}

export default NewsletterSubscribe
